function CertificateBox({ img1, img2, img3, img4, img5, img6, img7, img8, img9 }) {
    return (
        <>
            <section className="c-container">
                <div className="cert col-c row-1" > <img height="170px" src={img1} alt="" /></div>
                <div className="cert col1-2 row-2" ><img height="170px" src={img2} alt="" /></div>
                <div className="cert col2-2 row-2" ><img height="170px" src={img3} alt="" /></div>
                <div className="cert col1-3 row-3" ><img height="170px" src={img4} alt="" /></div>
                <div className="cert col2-3 row-3" ><img height="170px" src={img5} alt="" /></div>
                <div className="cert col3-3 row-3" ><img height="170px" src={img6} alt="" /></div>
                <div className="cert col1-2 row-4" ><img height="170px" src={img7} alt="" /></div>
                <div className="cert col2-2 row-4" ><img height="170px" src={img8} alt="" /></div>
                <div className="cert col-c row-5" > <img height="170px" src={img9}alt="" /></div>
            </section>
        </>
    )
}

