import React from 'react'
import { Link } from 'react-router-dom';
import emailjs from "emailjs-com";
import proImage6 from "./media/Project/project-image-6.png";
import "./css/App.css";

function Project6() {
  
    function sendEmail(e) {
      e.preventDefault();
  
      emailjs
        .sendForm(
          "gmail",
          "template_vlqgvc7",
          e.target,
          "user_QCp5pLhEifTk4hSflAOkZ"
        )
        .then(
          (result) => {
            console.log(result.text);
          },
          (error) => {
            console.log(error.text);
          }
        );
      alert("Your message sent successfully");
      e.target.reset();
    }
    return (
        <div className="project6">
            <div className="bakto-rot">
                <Link to="/" id="wrot">
                Jim |&#160;<span><Link to="/projects">Projects</Link></span>
                </Link>
            </div>

            <div className="project-container-div">
                <div className="project-div">
                    <a
                    href="https://invoice-jim.netlify.app/"
                    target="_blank"
                    rel="noreferrer"
                    >
                    <h2>Invoicing Web App</h2>
                    <figure>
                        <img src={proImage6} width="200" height="auto" alt="" />
                        <figcaption>
                        <strong>Invoicing</strong> Clone Using <em>React js</em>
                        </figcaption>
                    </figure>
                    </a>
                </div>
            </div>
            
            <form className="contact__form" onSubmit={sendEmail}>
                  <h2>Ask for Information</h2>
                  <input
                    type="hidden"
                    required
                    name="subject"
                    value="Genarel contact"
                  />
                  <label>Name</label>
                  <input type="text" required name="name" />
                  <label>Email</label>
                  <input type="email" required name="email" />
                  <input name="message" type="hidden" value="Need Info about Invoicing app" />
                  <button type="submit">Ask More Information</button>
                </form>

                <form className="contact__form" onSubmit={sendEmail}>
                  <h2>Report A problem</h2>
                  <input
                    type="hidden"
                    required
                    name="subject"
                    value="Genarel contact"
                  />
                  <label>Name</label>
                  <input type="text" required name="name" />
                  <label>Email</label>
                  <input type="email" required name="email" />
                  <label>Describe issue</label>
                  <input name="message" required />
                  <button type="submit">Submit</button>
                </form>
        </div>
    )
}

export default Project6
